import axiosInstance from '../../axios';
import axios from 'axios';
import * as actionTypes from './actionTypes';

export const authStart = () => {
    return {
        type: actionTypes.AUTH_START
    }
}

export const stopErase = () => {
    return {
        type: actionTypes.STOP_ERASE
    }
}

export const authSuccess = (username, token) => {
    return {
        type: actionTypes.AUTH_SUCCESS,
        token: token,
        username: username
    }
}

export const authFail = error => {       
    return {
        type: actionTypes.AUTH_FAIL,
        error: error
    }
}

export const logout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('username');
    localStorage.removeItem('expirationDate');     
    return {
        type: actionTypes.AUTH_LOGOUT
    }
}

export const checkAuthTimeout = expirationTime => {
    return dispatch => {
        setTimeout(() => {
            dispatch(logout());
        }, expirationTime * 1000)
    }
}

export const authLogin = (username, password) => {
    return dispatch => {
        dispatch(authStart());
        axios.post('https://qbared.com/api/users/auth/login/', {
            username: username,
            password: password
        })
        .then((response) => {           
            if (response !== undefined) {
                if (response.status === 200) {                   
                    const token = response.data.key;
                    const expirationDate = new Date(new Date().getTime() + 3600 * 1000);
                    localStorage.setItem('token', token);
                    localStorage.setItem("username", username);
                    localStorage.setItem('expirationdate', expirationDate);
                    dispatch(authSuccess(username, token));
                    dispatch(checkAuthTimeout(3600));
                    dispatch(getProfile(username));                   
                    dispatch(getServices(username));                                      
                } 
            } else {
                console.log(response)
            }
        })
        .catch(error => { 
            if (error.response) {
                if (error.response.status === 500) {
                    dispatch(authFail("Error en el servidor, vuelva a intentarlo"))
                } else if (error.response.status === 401 || error.response.status === 403) {
                    dispatch(authFail("DESAUTORIZADO"))
                } else {
                    var i;
                    for (i in error.response.data) {                    
                        dispatch(authFail(error.response.data[i][0]))
                    }  
                }              
            } else {
                console.log(error);                
            }            
        })
    }
}

export const authSignUp = (username, email, password1, password2) => {
    return dispatch => {
        dispatch(authStart());
        axios.post('https://qbared.com/api/users/auth/registration/', {
            username: username,
            email: email,
            password1: password1,
            password2: password2,
        })
        .then(res => {
            const token = res.data.key;
            const expirationDate = new Date(new Date().getTime() + 3600 * 1000);
            localStorage.setItem('token', token);
            localStorage.setItem("username", username);
            localStorage.setItem('expirationdate', expirationDate);
            dispatch(authSuccess(username, token));
            dispatch(checkAuthTimeout(3600));
            dispatch(getProfile(username));                   
            dispatch(getServices(username));
        })
        .catch(error => {   
            if (error.response) { 
                if (error.response.status === 500) {
                    dispatch(authFail("Error en el servidor, vuelva a intentarlo"))
                } else {              
                    var i;
                    for (i in error.response.data) {                    
                        dispatch(authFail(error.response.data[i][0]))
                    }
                }
            } else {
                console.log(error)
            }
        })
    }
}

export const authCheckState = () => {
    return dispatch => {
        const token = localStorage.getItem('token');
        const username = localStorage.getItem("username");
        if (token === undefined) {
            dispatch(logout());
        } else {
            const expirationDate = new Date(localStorage.getItem('expirationdate'));
            if ( expirationDate <= new Date()) {
                dispatch(logout());
            } else {
                dispatch(authSuccess(username, token));
                dispatch(checkAuthTimeout( (expirationDate.getTime() - new Date().getTime()) / 1000));
                dispatch(getProfile(username));                   
                dispatch(getServices(username));
            }
        }
    }
}

export const updateProfile = (profile) => {
    return {
        type: actionTypes.GET_PROFILE,
        profile: profile
    }
}

export const getProfile = (username) => {
    return dispatch => {
        axiosInstance
        .get(`users/profile/${ username }/`)
        .then((res) => {
            if (res !== undefined) {
                dispatch(updateProfile(res.data))            
            }
        })  
        .catch(error => {
            if (error.response) {
                if (error.response.status === 404) {
                    console.log("No se encontr el perfil")
                } else if (error.response.status === 401 || error.response.status === 403) {
                    console.log("Desautorizado")
                } else {
                    console.log(error.response)
                }
            } else {
                console.log(error)
            }
        })       
    }
}

export const updateServices = (services) => {
    return {
        type: actionTypes.GET_SERVICES,
        services: services
    }
}

export const getServices = (username) => {
    return dispatch => {
        axiosInstance
        .get(`servicios/${ username }/`)
        .then((res) => {
            if (res !== undefined) {
                dispatch(updateServices(res.data))            
            }
        })
        .catch(error => {
            if (error.response) {
                if (error.response.status === 404) {
                    console.log("No se encontr el servicio")                
                } else if (error.response.status === 401 || error.response.status === 403) {
                    console.log("Desautorizado")
                } else {
                    console.log(error.response)
                }
            } else {
                console.log(error)
            }
        })        
    }
}