import React from 'react';
import { Route } from 'react-router-dom';

import Lobby from './components/Lobby';
import Chat from './components/Chat';
import Login from './components/Login';
import Signup from './components/Signup';
import ResetPass from './components/users/ResetPass';
import NewPass from './components/users/NewPass';
import Perfil from './components/users/datosPerfil';
import Internet from './components/servicios/internet';
import JovenClub from './components/servicios/jovenClub';
import Emby from './components/servicios/emby';
import FileZilla from './components/servicios/fileZilla';
import Recarga from './components/coins/Recarga';
import Transfer from './components/coins/Transfer';
import UltimasOp from './components/coins/UltOpe';

const BaseRouter = () => {
    return(
        <div>
            <Route exact path='/portal/' component={Lobby} />
            <Route exact path='/portal/login/' component={Login} />
            <Route exact path='/portal/register/' component={Signup} />
            <Route exact path='/portal/resetpassword/' component={ResetPass} />
            <Route exact path='/portal/password-reset/confirm/:uid/:token/' component={NewPass} />
            <Route exact path='/chat/:chatID/' component={Chat} />
            <Route exact path='/portal/perfil/datos/' component={Perfil} />
            <Route exact path='/portal/servicios/internet/' component={Internet} />
            <Route exact path='/portal/servicios/joven-club/' component={JovenClub} />
            <Route exact path='/portal/servicios/emby/' component={Emby} />
            <Route exact path='/portal/servicios/ftp/' component={FileZilla} />
            <Route exact path='/portal/coins/recarga/' component={Recarga} />
            <Route exact path='/portal/coins/transferir/' component={Transfer} />
            <Route exact path='/portal/coins/ultimasop/' component={UltimasOp} />            
        </div>
    )
}

export default BaseRouter;