class WebSocketService {
    static instance = null;
    callbacks = {};
  
    static getInstance() {
      if (!WebSocketService.instance) {
        WebSocketService.instance = new WebSocketService();
      }
      return WebSocketService.instance;
    }
  
    constructor() {
      this.socketRef = null;
    }
  
    connect(chatID) {
      const path = `ws://172.20.24.2:8000/ws/chat/${chatID}/`;
      this.socketRef = new WebSocket(path);
      this.socketRef.onopen = () => {
        console.log('WebSocket open');
      };     
      this.socketNewMessage(JSON.stringify({
        command: 'fetch_messages',
        chatID: chatID
      }));
      this.socketRef.onmessage = e => {
        this.socketNewMessage(e.data);
      };
      this.socketRef.onerror = e => {
        console.log(e.message);
      };
      this.socketRef.onclose = () => {
        console.log("WebSocket closed let's reopen");
        this.connect();
      };
    }
  
    socketNewMessage(data) {
      const parsedData = JSON.parse(data);      
      const command = parsedData.command;
      if (Object.keys(this.callbacks).length === 0) {
        return;
      }
      if (command === 'messages') {
        this.callbacks[command](parsedData.messages, parsedData.participants);
      }
      if (command === 'message') {
        this.callbacks[command](parsedData.message);
      }
    }
  
    fetchMessages(chatID) {
      this.sendMessage({ command: 'fetch_messages', chatID: chatID });
    }
  
    newChatMessage(message) {
      this.sendMessage({ 
        command: 'new_message',
        chatID: message.chatID, 
        sender: message.sender, 
        message: message.content 
      }); 
    }
  
    addCallbacks(messagesCallback, newMessageCallback) {
      this.callbacks['messages'] = messagesCallback;
      this.callbacks['message'] = newMessageCallback;
    }
    
    sendMessage(data) {
      try {
        this.socketRef.send(JSON.stringify({ ...data }));
      }
      catch(err) {
        console.log(err.message);
      }  
    }
  
    state() {
      return this.socketRef.readyState;
    }
  
  }
  
  const WebSocketInstance = WebSocketService.getInstance();
  
  export default WebSocketInstance;