import React from 'react';
import axios from 'axios';
import { NavLink } from 'react-router-dom';
import { Form, Input, Button, Typography, Row, Col, message, Spin } from 'antd';
import { MailOutlined } from '@ant-design/icons';


const { Title } = Typography;

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

class ResetPass extends React.Component { 
  constructor(props) {
    super(props);
    this.state = {
        loading: false
    }
    this.onFinish = this.onFinish.bind(this);
  }

  onFinish = (values) => {
    axios.get(`https://qbared.com/api/users/email/${ values.email }/`)
    .then(response => {
      if (response.status === 200 ) {    
        axios.post(`https://qbared.com/api/users/auth/password/reset/`, {
          email : values.email,
        })
        .then(response => {
          if (response.status === 200 ) {            
            message.success("Link enviado, revise su correo");
            this.props.history.push('/portal/login/')
          }
        })
        .catch(error => {
          console.log(error);
          this.setState({loading:false})
        }) 
      }
      this.setState({loading:true})
    })  
    .catch(error => {
      if (error.response) {
        if (error.response.status === 404 ) {
          message.error("Email no encontrado");
          this.setState({loading:false})
        } else {
          console.log(error.response);
          this.setState({loading:false})
        }
      } else {
        console.log(error);
        this.setState({loading:false})
      }
    }) 
    this.setState({loading:true})
  };

  render() {                  
    return (
      <Spin tip="Cargando..." spinning={this.state.loading}>
        <Row>
          <Col span={14} offset={5} style={{ maxWidth: '500px', margin: 'auto', textAlign: 'center', background: 'white', padding: '20px', border: 'outset', boxShadow: '0 5px 10px 0'}}>          
            <Title level={3}>Escriba su Email:</Title>
            <Form
              {...formItemLayout}
              style={{maxWidth: '400px', margin: 'auto'}}          
              name="register"
              onFinish={this.onFinish}      
              scrollToFirstError
            >                         
              <Form.Item
                name="email"
                label="E-mail"
                rules={[
                  {
                    type: 'email',
                    message: 'El correo no es correcto',
                  },
                  {
                    required: true,
                    message: 'Por favor escriba su correo',
                  },
                ]}
              >
                <Input prefix={<MailOutlined/>} placeholder="Correo electrnico"/>
              </Form.Item>            
              <Form.Item {...tailFormItemLayout}>              
                <Button type="primary" htmlType="submit" style={{float: 'right'}}>
                  Enviar
                </Button>
              </Form.Item>
              <Button type="danger" htmlType="submit" style={{float: 'right'}}>
                <NavLink to='/portal/login/'>
                  Cancelar
                </NavLink>
              </Button>
            </Form>
          </Col>
        </Row>
      </Spin>
    );
  }
};


export default ResetPass;