import React from 'react';
import axios from 'axios';
import { Form, Input, Button, Typography, Row, Col, message, Spin } from 'antd';
import { LockOutlined } from '@ant-design/icons';

const { Title } = Typography;

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

class Newpass extends React.Component { 
  constructor(props) {
    super(props);
    this.state = {
        loading: false
    }
    this.onFinish = this.onFinish.bind(this);
  }

  componentDidUpdate() {
    if (this.props.error) {            
      message.error(this.props.error);
      this.props.stop()           
    }
  }

  onFinish = (values) => {
    const uid = this.props.match.params.uid;
    const token = this.props.match.params.token;
    axios.post(`https://qbared.com/api/users/auth/password/reset/confirm/`, {      
      uid: uid,
      token: token,
      new_password1: values.password,
      new_password2: values.confirm

    })
    .then(response => {
      if (response.status === 200 ) {
        message.success("Contrasea guardada con xito");
        this.props.history.push('/portal/login/')
      }
    })
    .catch(error => {
      if (error.response) {
        if (error.response.status === 400) {
          var i;
          for (i in error.response.data) {                    
            message.error(error.response.data[i][0])
          }        
        } else if (error.response.status === 500) {
          message.error("Error del servidor vuela a intentarlo")
          this.setState({loading:false})
        } else {
          console.log(error.response)
          this.setState({loading:false})
        }        
      } else {
        console.log(error)
      } this.setState({loading:false})
    })
    this.setState({loading:true})
  };

  render() {       
    return (
      <Spin tip="Cargando..." spinning={this.state.loading}>
        <Row>
          <Col span={14} offset={5} style={{maxWidth: '500px', margin: 'auto', textAlign: 'center', background: 'white', padding: '20px', border: 'outset', boxShadow: '0 5px 10px 0'}}>          
            <Title level={3}>Nueva contrasea:</Title>
            <Form
              {...formItemLayout}
              style={{maxWidth: '400px', margin: 'auto'}}          
              name="newpass"
              onFinish={this.onFinish}      
              scrollToFirstError
            >
              <Form.Item
                name="password"
                label="Contrasea"
                rules={[
                  {
                    required: true,                  
                    message: 'Por favor escriba su contrasea',
                  },
                ]}
                hasFeedback
              >
                <Input.Password prefix={<LockOutlined className="site-form-item-icon"/>} placeholder="Contrasea"/>
              </Form.Item>
              <Form.Item
                name="confirm"
                label="Repita la contrasea"
                dependencies={['password']}
                hasFeedback
                rules={[
                  {
                    required: true,
                    message: 'Por favor confirme su contrasea!',
                  },
                  ({ getFieldValue }) => ({
                    validator(_, value) {
                      if (!value || getFieldValue('password') === value) {
                        return Promise.resolve();
                      }

                      return Promise.reject('Las dos contraseas no coinciden!');
                    },
                  }),
                ]}
              >
                <Input.Password prefix={<LockOutlined className="site-form-item-icon"/>} placeholder="Confirme la contrasea"/>
              </Form.Item>
              <Form.Item {...tailFormItemLayout}>              
                <Button type="primary" htmlType="submit" style={{float: 'right'}}>
                  Guardar
                </Button>
              </Form.Item>            
            </Form>
          </Col>
        </Row>
      </Spin>
    );
  }
};

export default Newpass;