import React from 'react';
import { Redirect } from 'react-router-dom';
import axiosInstance from '../../axios';
import { connect } from 'react-redux';
import { Divider, Form, Input, InputNumber, Button, Row, Col, message, Spin, Statistic, Modal } from 'antd';
import { PayCircleOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import * as actions from '../../store/actions/auth';

const { confirm } = Modal;

const layout = {
    labelCol: { span: 12 },
    wrapperCol: { span: 12 },
};


class Transfer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {          
            loading : false       
        };             
        this.onFinish = this.onFinish.bind(this);        
    }

    onFinish = (values) => {        
        if (this.props.username === values.usuario) {           
            message.error("No puede ser su mismo usuario.");           
        } else if ( values.cantidad < 19 ) {            
            message.error("Mnimo 20 coins.");            
        }
        else {
            axiosInstance
                .get(`servicios/transfer/${ values.usuario }/`)
                .then((res) => {
                    if (res.status === 200) {
                        confirm({
                            title: `Seguro que desea transferir ${ values.cantidad } coins a ${ values.usuario }?`,
                            icon: <ExclamationCircleOutlined />,
                            content: 'La transferencia es permanente, no tiene devolucin.',
                            onOk: () => {                    
                                if ( this.props.profile.coins >= values.cantidad) {
                                    axiosInstance
                                    .post(`servicios/transfer/${ this.props.username }/`, {
                                        'usuario' : values.usuario,
                                        'cantidad': values.cantidad
                                    })
                                    .then((res) => {
                                        if ( res.status === 200) {                                
                                            message.success(res.data.mensaje);                                
                                            this.props.getProfile(this.props.username); 
                                            this.setState({loading: false});                                                   
                                        }
                                    })                       
                                    .catch((error) => {
                                        if (error.response) {                                
                                            if (error.response.status === 500) {
                                                message.error("Error del servidor, vuelva a intentarlo.");
                                                this.setState({loading: false});                                                      
                                            } else {
                                                console.log(error.response);
                                                this.setState({loading: false});                                    
                                            }
                                        } else {
                                            console.log(error);
                                            this.setState({loading: false});                             
                                        }                               
                                    });
                                    this.setState({loading: true});                                               
                                } else {                        
                                    message.error("No tiene suficientes coins.")
                                }
                            }
                        })
                    }
                })
                .catch((error) => {
                    if (error.response) {
                        if (error.response.status === 404) {
                            message.error("Usuario no existe");
                            this.setState({loading: false});                                         
                        } else if (error.response.status === 500) {
                            message.error("Error del servidor, vuelva a intentarlo.");
                            this.setState({loading: false});                                            
                        } else {
                            console.log(error.response);
                            this.setState({loading: false});                       
                        }
                    } else {
                        console.log(error);
                        this.setState({loading: false});                   
                    }                                               
                })
                this.setState({loading: true});
        }        
    }

    render () {
        if (this.props.isAuthenticated) {  
            return(
                <>
                <Divider orientation="center">Transferir Coins:</Divider>  
                <Spin tip="Cargando..." spinning={this.state.loading}>          
                <Row>                
                    <Col span={12} offset={6} style={{ maxWidth: '400px', margin: 'auto', background: 'white', padding: '15px', border: 'outset', boxShadow: '0 5px 10px 0' }}>
                        <Statistic style={{textAlign: 'center'}} prefix={<PayCircleOutlined />} title="Mis Coins:" value={this.props.profile.coins } />
                        <Divider orientation="center"></Divider> 
                        <Form {...layout} onFinish={this.onFinish}>
                            <Form.Item name="usuario" label="Hacia el Usuario" rules={[{ required: true, message: "Escriba el usuario de destino." }]}>
                                <Input />
                            </Form.Item>                       
                            <Form.Item name="cantidad" label="Cantidad" rules={[{ required: true, message: "Escriba la cantidad que va a enviar." }]}>
                                <InputNumber />
                            </Form.Item>
                            <Form.Item style={{textAlign: 'right'}}>
                                <Button type="primary" htmlType="submit">Transferir</Button>
                            </Form.Item> 
                        </Form>                    
                    </Col>                
                </Row>
                </Spin>
                </>
            )
        } else {
            return <Redirect to='/portal/login/' />
        }
    }
}

const mapStateToProps = state => {
    return {      
        username: state.username,
        isAuthenticated: state.token !== null,
        profile: state.profile,        
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getProfile: (username) => dispatch(actions.getProfile(username))        
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Transfer);