import React from 'react';
import { Redirect } from 'react-router-dom';
import axiosInstance from '../../axios';
import { connect } from 'react-redux';
import { Card, Badge, Button, Statistic, Form, message, Select, InputNumber, Input, Row, Col, Divider, PageHeader, Descriptions, Spin, Switch, Modal, Typography } from 'antd';
import { PayCircleOutlined, CloseOutlined, CheckOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import * as actions from '../../store/actions/auth';

const { Title, Text } = Typography;
const { Option } = Select;
const { confirm } = Modal;
const { Countdown } = Statistic;

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};

class Internet extends React.Component {
    constructor(props) {
        super(props); 
        this.state = {
            loading: false
        }      
        this.handleSwitch = this.handleSwitch.bind(this);       
        this.onFinish = this.onFinish.bind(this);
        this.timeOver = this.timeOver.bind(this);
    }

    onFinish = (values) => {
        if ( this.props.services.internet) {
            message.info('Ya tiene activo el servicio')            
        } else {
            if ( values.tipo === "internetHoras") {
                if (values.horas <= 1) {
                    message.error("Escriba las horas correctamente, mnimo 2 horas.")                    
                } else {
                    const costoHoras = values.horas * 10
                    if ( costoHoras <= this.props.profile.coins ) {
                        confirm({
                            title: 'Seguro que desea pagar el Internet x Horas?',
                            icon: <ExclamationCircleOutlined />,
                            content: `Son ${costoHoras} coins por ${values.horas} horas.`,
                            onOk: () => {                
                                axiosInstance
                                .put(`servicios/internet/${ this.props.username }/`, {                                                              
                                    int_horas: values.horas,
                                    cantidad: costoHoras,
                                    contra: values.contra,                                  
                                })
                                .then((response) => {
                                    if (response.status === 200) {                                                         
                                        message.success("Pago realizado con xito."); 
                                        this.props.getServices(this.props.username);
                                        this.props.getProfile(this.props.username);
                                        this.setState({loading: false})
                                    }
                                })                                
                                .catch((error) => {
                                    if (error.response) {                                        
                                        if (error.response.status === 400) {
                                            message.error("Ocurrio un error, actualice y vuelva a intentarlo.");
                                            this.setState({loading: false});                                        
                                        } else if (error.response.status === 500) {
                                            message.error("Error del servidor, vuelva a intentarlo.");
                                            this.setState({loading: false});                       
                                        } else {
                                            console.log(error.response);
                                            this.setState({loading: false});
                                        }
                                    } else {
                                        console.log(error);
                                        this.setState({loading: false});
                                    }                               
                                });
                                this.setState({loading: true})                                
                            }
                        });                        
                    } else {
                        message.error('No tiene suficientes coins, por favor recargue')
                    }
                }
            } else if ( values.tipo === "internetMensual") {
                if ( this.props.profile.coins >= 200) {                                                 
                    confirm({
                        title: 'Seguro que desea pagar por Internet Mensual?',
                        icon: <ExclamationCircleOutlined />,
                        content: 'Son 200 coins por 30 das.',
                        onOk: () => {
                            axiosInstance
                            .put(`servicios/internet/${ this.props.username }/`, {
                                contra: values.contra,
                                perfil: 'LOCAL_1m',
                                servicio: "internetMensual",      
                            })
                            .then((response) => {
                                if (response.status === 200) {
                                    message.success("Pago realizado con xito."); 
                                    this.props.getServices(this.props.username);
                                    this.props.getProfile(this.props.username);
                                    this.setState({loading: false});                                                                         
                                }
                            })                            
                            .catch((error) => {
                                if (error.response) {
                                    if (error.response.status === 500) {
                                        message.error("Error del servidor, vuelva a intentarlo.");
                                        this.setState({loading: false});
                                    } else if (error.response.status === 400) {
                                        message.error("Ocurrio un error, actualice y vuelva a intentarlo.");
                                        this.setState({loading: false});                    
                                    } else {
                                        console.log(error.response);
                                        this.setState({loading: false});
                                    }
                                } else {
                                    console.log(error);
                                    this.setState({loading: false});
                                }                               
                            });
                            this.setState({loading: true});
                        }
                    });                                                                               
                } else {
                    message.error('No tiene suficientes coins, por favor recargue')
                }
            } else {
                if ( this.props.profile.coins >= 300) {                    
                    confirm({
                        title: 'Seguro que desea pagar por Internet Semanal?',
                        icon: <ExclamationCircleOutlined />,
                        content: 'Son 300 coins por 7 das de internet puesto desde 11am hasta 3am.',
                        onOk: () => {                              
                            axiosInstance
                            .put(`servicios/internet/${ this.props.username }/`, {                                
                                contra: values.contra,
                                perfil: '1mb_para_PC2',
                                servicio: "internetSemanal",     
                            })
                            .then((response) =>{
                                if (response.status === 200) {
                                    message.success("Pago realizado con xito."); 
                                    this.props.getServices(this.props.username);
                                    this.props.getProfile(this.props.username);
                                    this.setState({loading: false});                          
                                }
                            })                            
                            .catch((error) => {
                                if (error.response) {
                                    if (error.response.status === 500) {
                                        message.error("Error del servidor, vuelva a intentarlo.");
                                        this.setState({loading: false});
                                    } else if (error.response.status === 400) {
                                        message.error("Ocurrio un error, actualice y vuelva a intentarlo.");
                                        this.setState({loading: false});                    
                                    } else {
                                        console.log(error.response);
                                        this.setState({loading: false});
                                    }
                                } else {
                                    console.log(error);
                                    this.setState({loading: false});
                                }                               
                            });
                            this.setState({loading: true});
                        }
                    });                                              
                } else {
                    message.error('No tiene suficientes coins, por favor recargue')
                } 
            }           
        }
    }

    timeOver = () => {
        axiosInstance
        .put(`servicios/internet/${ this.props.username }/`, {
            internet: false                                 
        })
        .then(res => {
            if (res.status === 200) {
                this.props.getServices(this.props.username);
                this.props.getProfile(this.props.username);
                this.setState({ loading:false }); 
            }
        })
        .catch((error) => {
            if (error.response) {
                if (error.response.status === 500) {
                    message.error("Error del servidor, vuelva a intentarlo.");
                    this.setState({loading: false});
                } else if (error.response.status === 400) {
                    message.error("Ocurrio un error, actualice y vuelva a intentarlo.");
                    this.setState({loading: false});                    
                } else {
                    console.log(error.response);
                    this.setState({loading: false});
                }
            } else {
                console.log(error);
                this.setState({loading: false});
            }                               
        });
        this.setState({ loading:true }); 
    }

    handleSwitch = (value) => {
        confirm({
            title: 'Seguro que desea cambiar esta opcin?',
            icon: <ExclamationCircleOutlined />,
            content: 'Si esta activada cuando finalice el tiempo se reanudar automticamente si tiene suficientes coins en la cartera.',
            onOk: () => {
                axiosInstance
                .put(`servicios/internet/${ this.props.username }/`, {
                    int_auto: value
                })
                .then((res) => {
                    if (res.status === 200) {
                        message.success("Compra automtica cambiada");
                        this.props.getServices(this.props.username);
                        this.setState({loading: false});               
                    }
                })        
                .catch((error) => {
                    if (error.response) {
                        if (error.response.status === 500) {
                            message.error("Error del servidor, vuelva a intentarlo.");
                            this.setState({loading: false});
                        } else if (error.response.status === 400) {
                            message.error("Ocurrio un error, actualice y vuelva a intentarlo.");
                            this.setState({loading: false});                    
                        } else {
                            console.log(error.response);
                            this.setState({loading: false});
                        }
                    } else {
                        console.log(error);
                        this.setState({loading: false});
                    }                               
                });
                this.setState({loading: true});
            }
        })    
    }

    fechaActual = new Date();
    fechaMes = this.fechaActual.setDate(this.fechaActual.getDate() + 30);  
    fechaMes = new Date(this.fechaMes)

    render () {
        if (this.props.isAuthenticated) {  
            return (
                <Spin tip="Cargando..." spinning={this.state.loading}>
                    <Row style={{ background: 'white', border: 'outset', boxShadow: '0 5px 10px 0' }}>
                        <Col span={12}>
                            <Card title="Internet">
                                <Card.Grid style={{ width: '100%', textAlign: 'center'}}>                                             
                                    <Text>Acceso a Internet, con las coins que gastes se compran horas nautas para poder estar ONLINE!!!</Text>
                                    <ul>
                                        <li>El semanal es con internet puesto desde 11am a 3am todos los das.</li>
                                        <li>El mensual es el portal nauta para poner su cuenta, no incluye internet.</li>
                                        <li>Por horas puede comprar las que quiera, mnimo dos y usarlas en el horario de 11am a 3am cuando desee.</li>
                                    </ul>                       
                                </Card.Grid>                                          
                                {
                                    this.props.services.internet ?
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>                           
                                        <Badge color="green" text="Activo" />
                                        {
                                            this.props.services.int_time ?
                                            <Countdown title="Tiempo restante" onFinish={this.timeOver} value={this.props.services.int_time} format="d:DD-HH:mm:ss" />
                                            :
                                            <p>El tiempo lo puede ver <a href="https://login.qbared.com/status">Aqui</a></p>
                                        }                            
                                    </Card.Grid>
                                    :
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>
                                        <Badge color="red" text="No Activo" />
                                    </Card.Grid> 
                                }                        
                            </Card> 
                        </Col>
                            <Col span={12} style={{ padding: '15px' }}>
                            <Form {...layout} onFinish={this.onFinish}>
                                <PageHeader title="Detalles:" extra={                                                    
                                    <Form.Item >
                                        <Button type="primary" htmlType="submit">Pagar</Button>
                                    </Form.Item>
                                    }>
                                    <Descriptions>
                                        <Descriptions.Item style={{width: '100%' }}>
                                            <Switch checkedChildren={<CheckOutlined />} unCheckedChildren={<CloseOutlined />} checked={ this.props.services.int_auto } onChange={ this.handleSwitch }/>
                                        </Descriptions.Item>
                                    </Descriptions>
                                    <Descriptions>
                                        <Descriptions.Item style={{width: '100%' }}>    
                                            <Title style={{width: '30%'}} level={5}>Pago automtico</Title>
                                            <Text style={{width: '70%'}}>*Tip: Active esta opcin y cuando acabe su tiempo se le pagar automticamente si tiene suficientes coins en la cartera.</Text>                     
                                        </Descriptions.Item>
                                    </Descriptions>                                
                                </PageHeader>                        
                                <Form.Item
                                        style={{ textAlign: 'right' }}
                                        shouldUpdate={(prevValues, currentValues) => prevValues.tipo !== currentValues.tipo}
                                    >               
                                    {({ getFieldValue }) => {
                                        return getFieldValue('tipo') !== "internetHoras" ?                            
                                        <Statistic prefix={<PayCircleOutlined />} title="Precio:" value={getFieldValue('tipo') === "internetMensual" ? 200 : 300} />                            
                                        :
                                        <Form.Item
                                        style={{ textAlign: 'right' }}
                                        shouldUpdate={(prevValues, currentValues) => prevValues.horas !== currentValues.horas}
                                        > 
                                            {({ getFieldValue }) => {
                                                return (getFieldValue('horas') ?                                                
                                                <Statistic prefix={<PayCircleOutlined />} title="Precio:" value={(getFieldValue('horas') * 10)} />
                                                :
                                                <p>Cuantas horas?</p> 
                                            )}}
                                        </Form.Item>                               
                                    }} 
                                </Form.Item>
                                <Divider></Divider>
                                <Form.Item name="tipo" label="Tipo de conexion" rules={[{ required: true, message: "Escoja un tipo de internet." }]}>
                                    <Select
                                        placeholder="Mensual, semanal o por hora?" 
                                    >
                                        <Option value="internetMensual">Mensual</Option>
                                        <Option value="internetSemanal">Semanal</Option>
                                        <Option value="internetHoras" name="horas">Por hora</Option>                                
                                    </Select>
                                </Form.Item>
                                <Form.Item
                                    noStyle
                                    shouldUpdate={(prevValues, currentValues) => prevValues.tipo !== currentValues.tipo}
                                    >
                                    {({ getFieldValue }) => {
                                        return getFieldValue('tipo') === 'internetHoras' ? (
                                            <Form.Item label="Horas:" name="horas" rules={[{ required: true, message: "Defina cuantas horas." }]}>
                                                <InputNumber/>
                                            </Form.Item>          
                                        ) : null;
                                    }}                         
                                </Form.Item>                            
                                <Form.Item name="contra" label="Contrasea" rules={[{ required: true, message: "Escriba una contrasea." }]}>
                                    <Input.Password placeholder="Escriba su contrasea" />
                                </Form.Item>
                                </Form>
                            </Col>                                                                  
                    </Row> 
                </Spin>                       
            )
        } else {
            return <Redirect to='/portal/login/' />
        }
    }
}

const mapStateToProps = state => {
    return {      
        username: state.username,
        isAuthenticated: state.token !== null,
        loading: state.loading,
        profile: state.profile,
        user: state.user,
        services: state.services
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getProfile: (username) => dispatch(actions.getProfile(username)),
        getServices: (username) => dispatch(actions.getServices(username))
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Internet);