import React from 'react';
import { Redirect } from 'react-router-dom';
import axiosInstance from '../../axios';
import { connect } from 'react-redux';
import { Divider, Form, Input, Button, Row, Col, message, Statistic, Spin } from 'antd';
import { PayCircleOutlined } from '@ant-design/icons';
import * as actions from '../../store/actions/auth';


class Recarga extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false
        }      
        this.onFinish = this.onFinish.bind(this);
    }

    onFinish = (values) => {        
        if (values.code.length === 8) {       
            axiosInstance
            .post(`servicios/recarga/${ values.code }/`, {
                usuario: this.props.username,                       
            })
            .then((response) => {
                if (response.status === 200) {  
                    this.setState({loading: false});                                     
                    message.success(response.data.mensaje); 
                    this.props.getProfile(this.props.username);                  
                } else if (response.status === 203) {
                    this.setState({loading: false});    
                    message.error(response.data.mensaje);                                      
                } else if (response.status === 208) {  
                    this.setState({loading: false});                 
                    message.error(response.data.mensaje);                                     
                }
            })           
            .catch((error) => {
                if (error.response) {
                    if (error.response.status === 500) {
                        message.error("Error del servidor, vuelva a intentarlo."); 
                        this.setState({loading: false});                                          
                    } else {
                        console.log(error.response);
                        this.setState({loading: false});                      
                    }
                } else {
                    console.log(error);
                    this.setState({loading: false});                    
                }                               
            })
            this.setState({loading: true})          
        } else {            
            message.error("Escriba 8 nmeros.");           
        }
    };    

    render() {
        if (this.props.isAuthenticated) {
            return(
                <>
                <Divider orientation="center">Recargar cuenta:</Divider>
                <Spin tip="Cargando..." spinning={this.state.loading}>
                <Row>
                    <Col span={12} offset={6} style={{ maxWidth: '300px', margin: 'auto', background: 'white', padding: '15px', border: 'outset', boxShadow: '0 5px 10px 0' }}>
                        <Statistic style={{textAlign: 'center'}} prefix={<PayCircleOutlined />} title="Mis Coins:" value={this.props.profile.coins } />
                        <Divider></Divider>
                        <Form onFinish={this.onFinish}>                      
                            <Form.Item name="code" label="Cdigo" rules={[{ required: true, message: "Escriba el nmero de la recarga." }]}>
                                <Input />
                            </Form.Item>
                            <Form.Item style={{textAlign: 'right'}}>
                                <Button type="primary" htmlType="submit">Enviar</Button>
                            </Form.Item> 
                        </Form>
                    </Col>
                </Row>
                </Spin>                       
                </>
            )
        } else {
            return <Redirect to='/portal/login/' />
        }
    }
}

const mapStateToProps = state => {
    return {      
        username: state.username,              
        isAuthenticated: state.token !== null,
        profile: state.profile,       
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getProfile: (username) => dispatch(actions.getProfile(username))        
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Recarga);