import React from 'react';
import { Carousel, Divider } from 'antd';

const contentStyle = {
  marginTop: '15px',
  height: '160px',
  color: '#fff',
  lineHeight: '160px',
  textAlign: 'center',
  background: '#364d79',
};

function App() {
  return (
    <div className="App" style={{textAlign: "center"}}> 
        <Carousel autoplay>
          <div>
            <h3 style={contentStyle}>
              Bienvenidos al Portal del usuario de la QbaRed, aqu podr administrar sus servicios y ver su estado.
            </h3>
          </div>
          <div>
            <h3 style={contentStyle}>
              Para comenzar seleccione en el men la opcin que desee.
              </h3>
          </div>
          <div>
            <h3 style={contentStyle}>Pgina en desarrollo, reportar los errores y tener calma :-)</h3>
          </div>          
        </Carousel>
        <Divider>Sobre los Coins:</Divider>
        <h3>Los coins son la forma de pagar por los servicios, para agregar coins a su cuenta debe recargar, las tarjetas de recargas las puede obtener con el Yanky o iVan, 1cup = 1coin.<br>
        </br> El dinero se recoge porque hace falta para el mantenimiento y mejora de la red. Saludos.</h3> 
        <Divider>Ultimos cambios:</Divider>
        <ul>
          <li>Mejoras de funcionalidad.</li>
          <li>Se agrega la opcion pago automtico.</li>
          <li>Mejora de diseo.</li>
          <li>Se agrega recuperar contrasea con email.</li>
        </ul>
    </div>
  );
}

export default App;
