import React from 'react';
import { Layout, Menu, Switch, Divider, Badge, Typography, Avatar } from 'antd';
import { PayCircleOutlined, UserOutlined } from '@ant-design/icons';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actions from '../store/actions/auth';

import Sidepanel from './Sidepanel';

const { Header, Content, Footer } = Layout;
const { Text } = Typography;

class CustomLayout extends React.Component {
    constructor(props) {
        super(props);
        this.state = {             
            theme: 'dark',                     
        }
        this.changeTheme = this.changeTheme.bind(this);        
    }

    componentDidUpdate() {
        if (this.props.isAuthenticated && this.props.profile === {}) {
            this.props.getProfile(this.props.username)
            this.props.getServices(this.props.username)
        }
    }

    changeTheme = value => {
        this.setState({
          theme: value ? 'dark' : 'light',
        });
    };
  
    render() {
        return(
            <Layout className="layout">
                <Header>
                    <Menu theme={ this.state.theme } mode="horizontal" defaultSelectedKeys={['1']} style={{fontSize: '15px', width: '100%'}}>
                    <Menu.Item><Switch checked={this.state.theme === 'dark'} onChange={this.changeTheme}
                    checkedChildren="Oscuro"
                    unCheckedChildren="Claro"
                    /></Menu.Item>
                    <Menu.Item key="1"><a href="https://qbared.com">QbaRed</a></Menu.Item>                                
                    {
                        this.props.isAuthenticated ?
                        <Menu.Item key="2" onClick={this.props.logout}>Salir</Menu.Item>                   
                        :
                        <>
                        <Menu.Item key="2"><Link to="/portal/login/">Entrar</Link></Menu.Item>                 
                        <Menu.Item key="3"><Link to="/portal/register/">Registrarse</Link></Menu.Item>
                        </>            
                    }
                    {
                        this.props.isAuthenticated ?               
                        <span style={{float: 'right', paddingRight: 40}}> 
                            <Link to="/portal/perfil/datos">                 
                                <Text type="success">{ this.props.username }</Text>
                            </Link> 
                            <Divider type='vertical' ></Divider>
                            <Link to="/portal/coins/recarga/">                
                                <Badge count={this.props.profile.coins} overflowCount={9999} style={{ backgroundColor: '#52c41a' }}/>
                            </Link>
                            <PayCircleOutlined />
                            <Divider type="vertical"></Divider>
                            <Link to="/portal/coins/ultimasop/">
                                <Badge count={ this.props.profile.notificaciones ? this.props.profile.notificaciones.length : 0 } overflowCount={24}>
                                    <Avatar size={40} src={ this.props.profile.imagen } icon={<UserOutlined />}/>
                                </Badge>
                            </Link>
                        </span> 
                        :
                        null
                    }                           
                    </Menu>                                          
                </Header>
                <Layout style={{ background: 'rgb(220, 230, 250)' }}>
                    {
                        this.props.isAuthenticated ?
                        <Sidepanel theme={ this.state.theme }/>
                        :
                        null           
                    } 
                    <Content className="site-layout-background" style={{ padding: 10, margin: 10}}>                
                        <div style={{ background: 'rgb(220, 230, 250)', padding: 24, minHeight: 280 }}>
                            {this.props.children}
                        </div>
                    </Content>
                </Layout>
                <Footer style={{ background: 'rgb(16, 29, 56)', textAlign: 'center', position: 'fixed', bottom: '0px', width: '100%', fontSize: '20px', color: 'white', fontStyle: 'italic' }}>QbaRed_Portal del Usuario-v2.1 2019-2021</Footer>
            </Layout>
        )
    }
}

const mapStateToProps = state => {
    return {      
        username: state.username,
        isAuthenticated: state.token !== null,
        profile: state.profile,
        services: state.services
    };
};

const mapDispatchToProps = dispatch => {
  return {
      logout: () => dispatch(actions.logout()),
      getProfile: (username) => dispatch(actions.getProfile(username)),
      getServices: (username) => dispatch(actions.getServices(username))
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CustomLayout));
