import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../utility';

const initialState = {
    token: null,
    username: null,
    error: null,
    loading: false,
    profile: {},
    services: {}
}

const authStart = (state, action) => {
    return updateObject(state, {
        error: null,
        loading: true
    })
}

const authSuccess = (state, action) => {
    return updateObject(state, {
        token: action.token,
        username: action.username,
        error: null,
        loading: false
    })
}

const authFail = (state, action) => {
    return updateObject(state, {
        error: action.error,
        loading: false
    })
}

const authLogout = (state, action) => {
    return updateObject(state, {
        token: null,
        username: null
    })
}

const getProfile = (state, action) => {
    return updateObject(state, {
        profile: action.profile
    })
}

const getServices = (state, action) => {
    return updateObject(state, {
        services: action.services
    })
}

const stopErase = (state, action) => {
    return updateObject(state, {
        loading: false,
        error: null
    })
}

const reducer = (state=initialState, action) => {
    switch(action.type) {
        case actionTypes.AUTH_START: return authStart(state, action);
        case actionTypes.AUTH_SUCCESS: return authSuccess(state, action);
        case actionTypes.AUTH_FAIL: return authFail(state, action);
        case actionTypes.AUTH_LOGOUT: return authLogout(state, action);
        case actionTypes.GET_PROFILE: return getProfile(state, action);
        case actionTypes.GET_SERVICES: return getServices(state, action);
        case actionTypes.STOP_ERASE: return stopErase(state, action);
        default:
            return state;
    }
}

export default reducer;