import React from 'react';
import { Redirect } from 'react-router-dom';
import axiosInstance from '../../axios';
import { connect } from 'react-redux';
import { Table, Divider, message, Spin } from 'antd';

class UltOpe extends React.Component {
    state = {
        opers : [], 
        loading: true       
    };

    columns = [
        {
            title: 'Code',
            dataIndex: 'code',
            width: 60,
        },
        {
            title: 'Tipo',
            dataIndex: 'tipo',
            width: 60,
        },
        {
            title: '# Recarga',
            dataIndex: 'codRec',
            width: 80,
        },
        {
            title: 'Servicio',
            dataIndex: 'servicio',
            width: 90,
        },
        {
            title: 'Cantidad',
            dataIndex: 'cantidad',
            width: 70,           
        },
        {
            title: 'Hacia/Desde',
            dataIndex: 'haciaDesde',
            width: 80,
        },
        {
            title: 'Fecha',
            dataIndex: 'fecha',
            width: 100,
        },      
    ];

    componentDidMount() {
        axiosInstance
        .get(`servicios/operaciones/${ this.props.username }/`)
        .then((res) => {
            this.setState({
                opers: res.data,
                loading: false                
            })                       
        })
        .catch((error) => {            
            if (error.response) {
                if (error.response.status === 500) {                               
                message.error("ERROR DEL SERVIDOR")                          
            }
            this.setState({loading: false});
        }});
    }

    render () {
        if (this.props.isAuthenticated) { 
            return(
                <>
                <Divider orientation="center">Ultimas 10 Operaciones</Divider>
                <Spin tip="Buscando..." spinning={this.state.loading}>             
                <Table style={{ background: 'white', border: 'outset', boxShadow: '0 5px 10px 0' }} columns={this.columns} dataSource={this.state.opers}/> 
                </Spin>                      
                <Divider></Divider> 
            </>
            )
        } else {
            return <Redirect to='/portal/login/' />
        }
    }
}

const mapStateToProps = state => {
    return {  
        isAuthenticated: state.token !== null,    
        username: state.username,
    };
};


export default connect(mapStateToProps)(UltOpe);