import React from 'react';
import { Redirect } from 'react-router-dom';
import axiosInstance from '../../axios';
import { connect } from 'react-redux';
import { Card, Badge, Button, Statistic, Form, message, Row, Col, PageHeader, Descriptions, Spin, Switch, Modal, Typography } from 'antd';
import { PayCircleOutlined, CloseOutlined, CheckOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import * as actions from '../../store/actions/auth';

const { Title, Text } = Typography;
const { Countdown } = Statistic;
const { confirm } = Modal;

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};

class JovenClub extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false
        }
        this.handleSwitch = this.handleSwitch.bind(this);        
        this.onFinish = this.onFinish.bind(this);
        this.timeOver = this.timeOver.bind(this);
    }

    onFinish = () => {        
        if ( this.props.services.jc) {
            message.info('Ya tiene activo el servicio');            
        } else {
            if ( this.props.profile.coins >= 100) {
                confirm({
                    title: 'Seguro que desea pagar por Joven Club?',
                    icon: <ExclamationCircleOutlined />,
                    content: 'Son 100 coins por 30 das.',
                    onOk: () => { 
                        axiosInstance
                        .put(`servicios/jovenclub/${ this.props.username }/`, {
                            coins: 100,                                                 
                        })
                        .then((response) => {
                            if (response.status === 200) {
                                message.success("Pago realizado con xito.");
                                this.props.getServices(this.props.username); 
                                this.props.getProfile(this.props.username); 
                                this.setState({ loading:false });                     
                            }
                        })                        
                        .catch((error) => {
                            if (error.response) {
                                if (error.response.status === 500) {
                                    message.error("Error del servidor, vuelva a intentarlo.");
                                    this.setState({ loading:false });                       
                                } if (error.response.status === 400) {
                                    message.error("Ocurrio un error, actualice y vuelva a intentarlo.");
                                    this.setState({loading: false});
                                } else {
                                    console.log(error.response);
                                    this.setState({ loading:false }); 
                                }
                            } else {
                                console.log(error);
                                this.setState({ loading:false });  
                            }                               
                        });
                        this.setState({loading:true});
                    }
                });                
            } else {
                message.error('No tiene suficientes coins, por favor recargue');                
            }
        }        
    }

    timeOver = () => {
        axiosInstance
        .put(`servicios/jovenclub/${ this.props.username }/`, {
            jc: false                                 
        })
        .then(res => {
            if (res.status === 200) {
                this.props.getServices(this.props.username);
                this.props.getProfile(this.props.username);
                this.setState({ loading:false }); 
            }
        })
        this.setState({ loading:true }); 
    }

    handleSwitch = (value) => {
        confirm({
            title: 'Seguro que desea cambiar esta opcin?',
            icon: <ExclamationCircleOutlined />,
            content: 'Si esta activada cuando finalice el tiempo se le aadiran 30 das si tiene suficientes coins en la cartera.',
            onOk: () => {
                axiosInstance
                .put(`servicios/jovenclub/${ this.props.username }/`, {
                    jc_auto: value
                })
                .then((res) => {
                    if (res.status === 200) {
                        message.success("Compra automtica cambiada");
                        this.props.getServices(this.props.username);
                        this.setState({loading: false});               
                    }
                })        
                .catch((error) => {
                    if (error.response) {
                        if (error.response.status === 500) {
                            message.error("Error del servidor, vuelva a intentarlo.");
                            this.setState({loading: false});                       
                        } else {
                            console.log(error.response);
                            this.setState({loading: false});
                        }
                    } else {
                        console.log(error);
                        this.setState({loading: false});
                    }                               
                });
                this.setState({loading: true});
            }
        })    
    }

    fechaActual = new Date();
    fechaMes = this.fechaActual.setDate(this.fechaActual.getDate() + 30);  
    fechaMes = new Date(this.fechaMes)

    render () {
        if (this.props.isAuthenticated) {
            return (            
                <Spin tip="Cargando..." spinning={this.state.loading}>
                    <Row style={{ background: 'white', border: 'outset', boxShadow: '0 5px 10px 0' }}>
                        <Col span={12}>                               
                            <Card title="Joven Club">
                                <Card.Grid style={{ width: '100%', textAlign: 'center'}}>Acceso a la red Joven Club. Se recogen coins para el mantenimiento de la infraestructura de la red.</Card.Grid>  
                                {
                                    this.props.services.jc ?
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>
                                        <Badge color="green" text="Activo" />
                                        <Countdown title="Tiempo restante" onFinish={this.timeOver} value={this.props.services.jc_time} format="d:D-HH:mm:ss" />
                                    </Card.Grid>
                                    :
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>
                                        <Badge color="red" text="No Activo" />
                                    </Card.Grid> 
                                }                   
                            </Card>               
                        </Col>                
                        <Col span={12}>
                            <PageHeader title="Detalles:" extra={
                                <Form {...layout} onFinish={this.onFinish}>                                
                                    <Form.Item style={{textAlign: 'right'}}>
                                        <Button type="primary" htmlType="submit">Pagar</Button>
                                    </Form.Item> 
                                </Form>}>
                                <Descriptions>
                                    <Descriptions.Item style={{width: '100%' }}>
                                        <Switch checkedChildren={<CheckOutlined />} unCheckedChildren={<CloseOutlined />} checked={ this.props.services.jc_auto } onChange={ this.handleSwitch }/>
                                    </Descriptions.Item>
                                </Descriptions>
                                <Descriptions>
                                    <Descriptions.Item style={{width: '100%' }}>    
                                        <Title style={{width: '30%'}} level={5}>Pago automtico</Title>
                                        <Text style={{width: '70%'}}>*Tip: Active esta opcin y cuando acabe su tiempo se le pagar automticamente si tiene suficientes coins en la cartera.</Text>                     
                                    </Descriptions.Item>
                                </Descriptions>
                                <Descriptions>  
                                    <Descriptions.Item style={{width: '100%' }}>Si es primera vez que va a usar JovenClub escribirle al Yanky.</Descriptions.Item>
                                </Descriptions>                         
                                <Descriptions>                                
                                    <Descriptions.Item label="Desde" style={{width: '50%' }}>{new Date().toLocaleString()}</Descriptions.Item>
                                    <Descriptions.Item label="Hasta" style={{width: '50%' }}>{this.fechaMes.toLocaleString()}</Descriptions.Item>                             
                                </Descriptions>                             
                                <Statistic style={{textAlign: 'right'}} prefix={<PayCircleOutlined />} title="Precio:" value="100" />                                       
                            </PageHeader>
                        </Col>
                    </Row>                
                </Spin>  
            )
        } else {
            return <Redirect to='/portal/login/' />
        }
    }
}

const mapStateToProps = state => {
    return {      
        username: state.username,
        isAuthenticated: state.token !== null,
        profile: state.profile,
        services: state.services
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getProfile: (username) => dispatch(actions.getProfile(username)),
        getServices: (username) => dispatch(actions.getServices(username))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(JovenClub);