import { Widget, addResponseMessage } from 'react-chat-widget';
import React from 'react';
import { connect } from 'react-redux';

import WebSocketInstance from '../websocket';

import 'react-chat-widget/lib/styles.css';

class Chat extends React.Component {
    constructor(props) {
        super(props);
        this.state = {                   
        }   
        this.waitForSocketConnection(() => {
          WebSocketInstance.addCallbacks(this.setMessages.bind(this), this.addMessage.bind(this))
          WebSocketInstance.fetchMessages(this.props.match.params.chatID);
        })        
    }

    componentDidMount() {
        WebSocketInstance.connect(this.props.match.params.chatID);
    }
    
    waitForSocketConnection(callback) {
        const component = this;
        setTimeout(
            function () {
            if (WebSocketInstance.state() === 1) {
                console.log("Connection is made")
                callback();
                return;
            } else {
                console.log("wait for connection...")
                component.waitForSocketConnection(callback);
            }
        }, 100);
    }
    //agregar el sms a messages en estado
    addMessage = (message) => {
        this.setState({ messages: [...this.state.messages, message]});
        if (message.sender !== this.props.username) {
            addResponseMessage(message.content)  
        }             
    }  
    //mensaje que llegan de atras
    setMessages = (messages, participants) => {        
        this.setState({ messages: messages.reverse()});
        this.state.messages.map((message) => (
            addResponseMessage(message.content)
        ));
        participants.map(this.senderReceiver)
    }

    senderReceiver = (p) => {
        if (p.user === this.props.username) {
            this.setState({ sender: p.user})
        } else {
            this.setState({ receiver: p.user})
        }
    }
    
    messageChangeHandler = (event) =>  {
        this.setState({
            message: event.target.value
        })
    }
    
    sendMessageHandler = (e) => {
        e.preventDefault();
        const messageObject = {
            chatID: this.props.match.params.chatID,
            sender: this.props.username,
            content: this.state.message,
        };
        WebSocketInstance.newChatMessage(messageObject);
        this.setState({
            message: ''
        });
    }
    
    renderMessages = (messages) => {
        const currentUser = this.props.username;
        return messages.map((message, i) => (
            <li 
                key={message.id} 
                style={{textAlign: message.sender === currentUser ? 'right' : 'left'}}>                
                <p>{message.content}
                    <br/>
                    <small style={{'color': message.sender === currentUser ? 'red' : 'blue'}}>
                    {Math.round((new Date().getTime() - new Date(message.timestamp).getTime())/60000)} minutes ago
                    </small>
                </p>
            </li>
        ));
    }

    llegoMessage = (newMessage) => {
        addResponseMessage(newMessage)
    };

    handleNewUserMessage = (newMessage) => {
        const messageObject = {
            chatID: this.props.match.params.chatID,
            sender: this.props.username,
            content: newMessage,
        };
        WebSocketInstance.newChatMessage(messageObject);
        this.setState({
            message: ''
        });        
    };

    render() {    
        const messages = this.state.messages;
        return (
            <div id="frame">                
                <div className="content">                   
                    <div className="messages">
                        <ul id="chat-log" style={{listStyle: 'none'}}>
                        { 
                            messages && 
                            this.renderMessages(messages) 
                        }
                        </ul>
                    </div>
                    <div className="message-input">
                        <form onSubmit={this.sendMessageHandler}>                            
                            <input
                                onChange={this.messageChangeHandler}
                                value={this.state.message}
                                required
                                id="chat-message-input"
                                type="text"
                                placeholder="Write your message..."
                            />
                            <button id="chat-message-submit" className="submit">
                                Enviar
                            </button>                           
                        </form>
                    </div>
                </div>                     
                <Widget
                    title={this.state.receiver}
                    subtitle={this.state.sender}
                    senderPlaceHolder="Escriba un mensaje..."
                    profileAvatar="https://blog-assets.hootsuite.com/wp-content/uploads/2022/03/free-stock-photos-6-620x412.jpg"
                    titleAvatar="https://upload.wikimedia.org/wikipedia/commons/b/bb/NGC_1309_SDSS.jpg"
                    handleNewUserMessage={this.handleNewUserMessage}
                />                
            </div>          
        );
    };
}

const mapStateToProps = state => {
    return {
      token: state.token,
      username: state.username
    };
};


export default connect(mapStateToProps)(Chat);
        