import React from 'react';
import { Redirect } from 'react-router-dom';
import axiosInstance from '../../axios';
import { connect } from 'react-redux';
import { QuestionCircleOutlined  } from '@ant-design/icons';
import { Card, Divider, message, Spin, Form, Input, Tooltip, Row, Col, Button, Typography } from 'antd';

const { Title } = Typography

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};

class DatosPerfil extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            user: {},
            loading: true
        }
        this.getUser = this.getUser.bind(this);
        this.onFinish = this.onFinish.bind(this);      
    }

    getUser = () => {
        axiosInstance
        .get(`users/details/${ this.props.username}/`) 
        .then((response) => {
            if (response.status === 200) {
                this.setState({
                    user: response.data,
                    loading: false
                })                
            }
        })
    }

    componentDidMount() {
        this.getUser()
    }

    onFinish = (values) => {        
        axiosInstance
        .put(`users/details/${ this.props.username }/`, {
            email: values.email,
            first_name: values.nombre,
            last_name: values.apellidos
        })
        .then((response) => {
            if (response.status === 200) {
                message.success("Perfil editado correctamente.");
                this.getUser();                
            }
        })        
        .catch((error) => {
            if (error.response) {
                if (error.response.status === 500) {
                    message.error("Error del servidor, vuelva a intentarlo.");
                    this.setState({loading:false});                       
                } else {
                    console.log(error.response);
                    this.setState({loading:false});
                }
            } else {
                console.log(error);
                this.setState({loading:false});
            }                               
        })
        this.setState({loading:true}); 
    };
    
    render() {
        if (this.props.isAuthenticated) {
            return (            
                <Spin tip="Cargando..." spinning={this.state.loading}>
                    <Row style={{ background: 'white', padding: '15px', border: 'outset', boxShadow: '0 5px 10px 0' }}>           
                        <Col span={12}>
                            <Card title="Perfil">                    
                                    <Card.Grid style={{fontSize: 15, width: '100%', float: 'left'}}>Usuario:<Divider type="vertical"/><span style={{fontSize: 25, }}>{this.state.user.username}</span></Card.Grid> 
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>Fecha de registro: {`${new Date(this.state.user.date_joined)}`}</Card.Grid>
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>Ultima visita: {`${new Date(this.state.user.last_login)}`}</Card.Grid>
                            </Card>
                        </Col> 
                        <Col span={12}>
                            <Title level={4} style={{ textAlign: 'center' }}>Editar mis datos:</Title>
                            <Form {...layout} onFinish={this.onFinish}                        
                            scrollToFirstError
                            >
                            <Form.Item name="nombre" label={
                                <span>
                                    Nombre&nbsp;
                                    <Tooltip title="Su nombre real?">
                                    <QuestionCircleOutlined />
                                    </Tooltip>
                                </span>
                                }
                                rules={[{ required: true, message: 'Por favor escriba su nombre!', whitespace: true,},]}
                            >
                                <Input placeholder={this.state.user.first_name} />
                            </Form.Item>
                            <Form.Item name="apellidos" label={
                                <span>
                                    Apellidos&nbsp;
                                    <Tooltip title="Sus apellidos?">
                                    <QuestionCircleOutlined />
                                    </Tooltip>
                                </span>
                                }
                                rules={[{ required: true, message: 'Por favor escriba su apellido!', whitespace: true,},]}
                            >
                                <Input placeholder={this.state.user.last_name} />
                            </Form.Item>
                            
                            <Form.Item name="email" label="Correo: " rules={[{ type: 'email', message: 'Esto no es un correo valido!',},{ required: true, message: 'Por favor escrbiba un correo.',},]}>
                                <Input placeholder={this.state.user.email}/>
                            </Form.Item>

                        {/*  <Form.Item
                                name="movil"
                                label="Movil:"
                                rules={[
                                {
                                    required: true,
                                    message: 'Por favor escriba su movil!',
                                },
                                ]}
                            >
                                <Input
                                addonBefore="+53"
                                style={{
                                    width: '100%',
                                }} placeholder={this.state.user.user_name}
                                />
                            </Form.Item> */}

                            <Form.Item style={{textAlign: 'right'}}>
                                <Button type="primary" htmlType="submit">
                                Guardar
                                </Button>
                            </Form.Item>
                            </Form>
                        </Col>
                    </Row>                              
                </Spin>            
            )
        } else {
            return <Redirect to='/portal/login/' />
        }
    }    
}

const mapStateToProps = state => {
    return {      
        username: state.username,
        isAuthenticated: state.token !== null,
    };
};

export default connect(mapStateToProps)(DatosPerfil);


