import React from 'react';
import { Link } from 'react-router-dom';
import { Layout, Menu  } from 'antd';
import { LaptopOutlined, PayCircleOutlined, UserOutlined } from '@ant-design/icons';

const {  Sider } = Layout;
const { SubMenu } = Menu;

class SidePanel extends React.Component {

    render() {
        return(
            <Sider 
                breakpoint="lg"
                collapsedWidth="0"
                width={200} className="site-layout-background">                
                <Menu
                    theme={this.props.theme}
                    mode="inline"
                    defaultSelectedKeys={['1']}
                    defaultOpenKeys={['sub1', 'sub2', 'sub3']}
                    style={{ height: '100%', borderRight: 0 }}
                >
                    <SubMenu key="sub1" icon={<UserOutlined />} title=" Perfil">
                    <Menu.Item key="1"><Link to="/portal/perfil/datos/">Mi Perfil</Link></Menu.Item>                                                  
                    </SubMenu>
                    <SubMenu key="sub2" icon={<LaptopOutlined />} title="Servicios">
                    <Menu.Item key="3"><Link to="/portal/servicios/internet/">Internet</Link></Menu.Item>
                    <Menu.Item key="4"><Link to="/portal/servicios/joven-club/">Joven Club</Link></Menu.Item>
                    <Menu.Item key="5"><Link to="/portal/servicios/emby/">Emby</Link></Menu.Item>
                    <Menu.Item key="6"><Link to="/portal/servicios/ftp/">FileZilla</Link></Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub3" icon={<PayCircleOutlined />} title="Mis Coins">
                    <Menu.Item key="7"><Link to="/portal/coins/recarga/">Recargar</Link></Menu.Item>
                    <Menu.Item key="8"><Link to="/portal/coins/transferir/">Transferir</Link></Menu.Item> 
                    <Menu.Item key="9"><Link to="/portal/coins/ultimasop/">Ultimas Operaciones</Link></Menu.Item>                                
                    </SubMenu>
                </Menu>
            </Sider>
        )
    }
}

export default SidePanel;