import React from 'react';
import { Redirect } from 'react-router-dom';
import axiosInstance from '../../axios';
import { connect } from 'react-redux';
import { Card, Badge, Button, Statistic, Form, message, Row, Col, PageHeader, Descriptions, Spin, Input, Switch, Modal, Typography } from 'antd';
import { PayCircleOutlined, CloseOutlined, CheckOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import * as actions from '../../store/actions/auth';


const { Title, Text } = Typography;
const { Countdown } = Statistic;
const { confirm } = Modal;

class FileZilla extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false
        }
        this.handleSwitch = this.handleSwitch.bind(this);               
        this.onFinish = this.onFinish.bind(this);
        this.timeOver = this.timeOver.bind(this);
    }

    onFinish = (values) => {                  
        if ( this.props.services.ftp) {            
            message.info('Ya tiene activo el servicio');            
        } else {
            if ( this.props.profile.coins >= 50) {                
                confirm({
                    title: 'Seguro que desea pagar por FileZilla?',
                    icon: <ExclamationCircleOutlined />,
                    content: 'Son 50 coins por 30 das.',
                    onOk: () => {               
                        axiosInstance
                        .put(`servicios/filezilla/${ this.props.username }/`, {
                            contra: values.contra                                    
                        })
                        .then((response) => {
                            if (response.status === 200) {                        
                                message.success("Pago realizado con xito.");
                                this.props.getServices(this.props.username); 
                                this.props.getProfile(this.props.username); 
                                this.setState({ loading:false });                      
                            }
                        })                        
                        .catch((error) => {
                            if (error.response) {
                                if (error.response.status === 500) {
                                    message.error("Error del servidor, vuelva a intentarlo.");
                                    this.setState({ loading:false });                        
                                } else {
                                    console.log(error.response);
                                    this.setState({ loading:false });                        
                                }
                            } else {
                                console.log(error);
                                this.setState({ loading:false });                        
                            }                               
                        });
                        this.setState({ loading:true });            
                    }                   
                });            
            } else {               
                message.error('No tiene suficientes coins, por favor recargue')
            }
        }              
    }

    timeOver = () => {
        axiosInstance
        .put(`servicios/filezilla/${ this.props.username }/`, {
            ftp: false                                 
        })
        .then(res => {
            if (res.status === 200) {
                this.props.getServices(this.props.username);
                this.props.getProfile(this.props.username);
                this.setState({ loading:false }); 
            }
        })
        .catch((error) => {
            if (error.response) {
                if (error.response.status === 500) {
                    message.error("Error del servidor, vuelva a intentarlo.");
                    this.setState({ loading:false });                        
                } else {
                    console.log(error.response);
                    this.setState({ loading:false });                        
                }
            } else {
                console.log(error);
                this.setState({ loading:false });                        
            }                               
        });
        this.setState({ loading:true });
    }

    handleSwitch = (value) => {
        confirm({
            title: 'Seguro que desea cambiar esta opcin?',
            icon: <ExclamationCircleOutlined />,
            content: 'Si esta activada cuando finalice el tiempo se aadirn 30 das si tiene suficientes coins en la cartera.',
            onOk: () => {
                axiosInstance
                .put(`servicios/filezilla/${ this.props.username }/`, {
                    ftp_auto: value
                })
                .then((res) => {
                    if (res.status === 200) {
                        message.success("Compra automtica cambiada");
                        this.props.getServices(this.props.username);
                        this.setState({loading: false});                
                    }
                })        
                .catch((error) => {
                    if (error.response) {
                        if (error.response.status === 500) {
                            message.error("Error del servidor, vuelva a intentarlo.");
                            this.setState({ loading:false });                        
                        } else {
                            console.log(error.response);
                            this.setState({ loading:false });                        
                        }
                    } else {
                        console.log(error);
                        this.setState({ loading:false });                        
                    }                               
                });
                this.setState({loading: true});
            }
        })    
    }

    fechaActual = new Date();
    fechaMes = this.fechaActual.setDate(this.fechaActual.getDate() + 30);  
    fechaMes = new Date(this.fechaMes)

    render () {
        if (this.props.isAuthenticated) {
            return (            
                <Spin tip="Cargando..." spinning={this.state.loading}>
                    <Row style={{ background: 'white', border: 'outset', boxShadow: '0 5px 10px 0' }}>
                        <Col span={12}>                     
                            <Card title="FileZilla-FTP" > 
                                <Card.Grid style={{ width: '100%', textAlign: 'center'}}>
                                    Descarga de contenido desde el servidor. Paquete semanal y otros.<br></br><a href="https://qbared.com/ftp/">Ms informacin, click aqu!!!</a>
                                </Card.Grid>                                          
                                {
                                    this.props.services.ftp ?
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>
                                        <Badge color="green" text="Activo" />
                                        <Countdown title="Tiempo restante" onFinish={this.timeOver} value={this.props.services.ftp_time} format="d:D-HH:mm:ss" />
                                    </Card.Grid>
                                    :
                                    <Card.Grid style={{ width: '100%', textAlign: 'center'}}>
                                        <Badge color="red" text="No Activo" />
                                    </Card.Grid> 
                                }                                                       
                            </Card>  
                        </Col>
                        <Col span={12}>
                            <Form onFinish={this.onFinish}> 
                            <PageHeader title="Detalles:" extra={                  
                                    <Form.Item >
                                        <Button htmlType="submit" type="primary">Pagar</Button>
                                    </Form.Item>}> 
                                <Descriptions>
                                    <Descriptions.Item style={{width: '100%' }}>
                                        <Switch checkedChildren={<CheckOutlined />} unCheckedChildren={<CloseOutlined />} checked={ this.props.services.ftp_auto } onChange={ this.handleSwitch }/>
                                    </Descriptions.Item>
                                </Descriptions>
                                <Descriptions>
                                    <Descriptions.Item style={{width: '100%' }}>    
                                        <Title style={{width: '30%'}} level={5}>Pago automtico</Title>
                                        <Text style={{width: '70%'}}>*Tip: Active esta opcin y cuando acabe su tiempo se le pagar automticamente si tiene suficientes coins en la cartera.</Text>                     
                                    </Descriptions.Item>
                                </Descriptions>
                                <Descriptions>                                
                                    <Descriptions.Item label="Desde" style={{width: '50%' }}>{new Date().toLocaleString()}</Descriptions.Item>
                                    <Descriptions.Item label="Hasta" style={{width: '50%' }}>{this.fechaMes.toLocaleString()}</Descriptions.Item>                            
                                </Descriptions>
                                <Statistic style={{textAlign: 'right'}} prefix={<PayCircleOutlined />} title="Precio:" value="50" />              
                                <Form.Item name="contra" label="Contrasea: " rules={[{required: true, message: 'Escriba una contrasea'}]} >
                                    <Input.Password placeholder="Escriba su contrasea"/>
                                </Form.Item>
                            </PageHeader>
                            </Form>                    
                        </Col>                                           
                    </Row>
                </Spin> 
            )
        } else {
            return <Redirect to='/portal/login/' />
        }
    }
}

const mapStateToProps = state => {
    return {      
        username: state.username,
        isAuthenticated: state.token !== null,
        loading: state.loading,
        profile: state.profile,
        user: state.user,
        services: state.services
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getProfile: (username) => dispatch(actions.getProfile(username)),
        getServices: (username) => dispatch(actions.getServices(username))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(FileZilla);