import React from 'react';
import { NavLink, Redirect } from 'react-router-dom';
import { Form, Input, Button, Typography, Row, Col, message } from 'antd';
import { UserOutlined, LockOutlined, MailOutlined } from '@ant-design/icons';
import { connect } from 'react-redux';
import * as actions from '../store/actions/auth';

const { Title } = Typography;

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

class Signup extends React.Component { 
  
  componentDidUpdate() {
    if (this.props.error) {            
      message.error(this.props.error);
      this.props.stop()           
    }
  }

  onFinish = (values) => {
    this.props.onAuth(values.username, values.email, values.password, values.confirm);    
  };

  render() {   
    if (this.props.isAuthenticated) {
      return <Redirect to='/portal/' />
    }               
    return (
      <Row>
        <Col span={14} offset={5} style={{maxWidth: '600px', margin: 'auto', textAlign: 'center', background: 'white', padding: '20px', border: 'outset', boxShadow: '0 5px 10px 0'}}>          
          <Title level={3}>Datos de registro:</Title>
          <Form
            {...formItemLayout}
            style={{maxWidth: '500px', margin: 'auto'}}          
            name="register"
            onFinish={this.onFinish}      
            scrollToFirstError
          >
            <Form.Item label="Nombre de usuario" name="username"
              rules={[
              {
                  required: true,
                  message: 'Por favor escriba su nombre de usuario',
                  whitespace: true
              },
              ]}
            >
              <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Nombre de usuario"/>
            </Form.Item>              
            <Form.Item
              name="email"
              label="E-mail"
              rules={[
                {
                  type: 'email',
                  message: 'El correo no es correcto',
                },
                {
                  required: true,
                  message: 'Por favor escriba su correo',
                },
              ]}
            >
              <Input prefix={<MailOutlined/>} placeholder="Correo electrnico"/>
            </Form.Item>
            <Form.Item
              name="password"
              label="Contrasea"
              rules={[
                {
                  required: true,                  
                  message: 'Por favor escriba su contrasea',
                },
              ]}
              hasFeedback
            >
              <Input.Password prefix={<LockOutlined className="site-form-item-icon"/>} placeholder="Contrasea"/>
            </Form.Item>
            <Form.Item
              name="confirm"
              label="Repita la contrasea"
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: 'Por favor confirme su contrasea!',
                },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }

                    return Promise.reject('Las dos contraseas no coinciden!');
                  },
                }),
              ]}
            >
              <Input.Password prefix={<LockOutlined className="site-form-item-icon"/>} placeholder="Confirme la contrasea"/>
            </Form.Item>
            <Form.Item {...tailFormItemLayout}>              
              <Button type="primary" htmlType="submit" style={{float: 'right'}}>
                Registrarse
              </Button>
            </Form.Item>
            <Button htmlType="submit" style={{width: '100%'}}>
              <NavLink to='/portal/login/'>
                Ya tengo cuenta
              </NavLink>
            </Button>
          </Form>
        </Col>
      </Row>
    );
  }
};

const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.token !== null,
    loading: state.loading,
    error: state.error
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onAuth: (username, email, password1, password2) => dispatch(actions.authSignUp(username, email, password1, password2)),
    stop: () => dispatch(actions.stopErase())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Signup);