import React from 'react';
import { connect } from 'react-redux';
import { NavLink, Redirect } from 'react-router-dom';
import * as actions from '../store/actions/auth';
import { Form, Input, Button, Spin, Typography, Row, Col, message } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';

const { Title } = Typography;

class Login extends React.Component {

    componentDidUpdate() {
        if (this.props.error) {            
            message.error(this.props.error);
            this.props.stop()           
        }
    }

    onFinish = (values) => {
        this.props.onAuth(values.username, values.password);
    };

    render () {        
        if (this.props.isAuthenticated) {
            return <Redirect to='/portal/' />
        }
        return (
            <Row>
                <Col span={14} offset={5} style={{maxWidth:'500px', margin: 'auto', textAlign: 'center', background: 'white', padding: '20px', border: 'outset', boxShadow: '0 5px 10px 0' }}>                
                    <Spin tip="Cargando..." spinning={this.props.loading}>
                        <Title level={3}>Bienvenido:</Title>                   
                        <Form                
                            name="normal_login"
                            className="login-form"
                            initialValues={{remember: true}}  
                            style={{maxWidth: '300px', margin: 'auto'}}
                            onFinish={this.onFinish}            
                            >
                            <Form.Item                
                                name="username"                
                                rules={[{ required: true, message: 'Por favor escriba su nombre de usuario!'}]}>
                                <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Usuario"/>
                            </Form.Item>
                            <Form.Item                
                                name="password"               
                                rules={[{ required: true, message: 'Por favor escriba su contrasea!'}]}>
                                <Input.Password  prefix={<LockOutlined className="site-form-item-icon"/>} placeholder="Contrasea"/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit" style={{width: '100%'}}>
                                Entrar
                                </Button>                    
                            </Form.Item>                            
                            <Button htmlType="submit" style={{width: '100%'}}>
                                <NavLink to='/portal/register/'>
                                    Crear una cuenta
                                </NavLink>
                            </Button>
                            <Button type="danger" htmlType="submit" style={{width: '100%'}}>
                                <NavLink to='/portal/resetpassword/'>
                                    Olvid mi contrasea
                                </NavLink>  
                            </Button>                                      
                        </Form>                    
                    </Spin>
                </Col>
            </Row>
        );
    }
};

const mapStateToProps = (state) => {
    return {
        isAuthenticated: state.token !== null,
        loading: state.loading,
        error: state.error,
        username: state.username
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAuth: (username, password) => dispatch(actions.authLogin(username, password)),
        stop: () => dispatch(actions.stopErase())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);